import React from "react";
import styles from "../styles/about.css";
console.log(styles);

import Container from "../components/container";

const pStyle = {
    textAlign: 'justify'
};

const User = props => 
    <div className={styles.user}>
        <div className={styles.description}>
            <h3 className={styles.username}>
                Autor: {props.username}
            </h3>
            <p className={styles.excerpt}>
                {props.excerpt}
            </p>
        </div>
    </div>
export default ({ data }) => (
    <Container>
        <h1>Acerca de {data.site.siteMetadata.title}</h1>
        <p style = {pStyle} >Este sitio pretende ser una herramienta y bitacora, para dar seguimiento a la clase de computaci칩n blanda 
            dictada por el profesor Gilberto Vargas Cano en la Universidad Tecnol칩gica de Pereira, en el
            segundo semestre del a침o 2018.
            Dentro de este blog se publicaran Papers y codigos de aplicaciones en el campo de la Inteligencia Artificial 
        </p>
        <User
            username={data.site.siteMetadata.author}
            avatar="https://s3.amazonaws.com/uifaces/faces/twitter/adellecharles/128.jpg"
            excerpt="Estudiante de Ingenieria en Sistemas y Computaci칩n UTP"
        />
    </Container>
);

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        title
        author
      }
    }
  }
`



